import Vue from 'vue'
import Router from 'vue-router'
import Landing from '../pages/LandingPage.vue'
import Game from '../pages/Game.vue'
import DetailPagePlayer from '../pages/DetailPagePlayer.vue'
import PlayerPage from '../pages/PlayerPage.vue'
import DetailPageTeam from '../pages/DetailPageTeam.vue'
import TeamPage from '../pages/TeamPage.vue'
import TournamentPage from '../pages/TournamentPage.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/landing',
      name: 'landing',
      component: Landing,
      meta: { hideHeader: true }
    }, {
      path: '/:game',
      name: 'game',
      component: Game
    }, {
      path: '/player/:detail',
      name: 'detail_player',
      component: DetailPagePlayer
    }, {
      path: '/player',
      name: 'player',
      component: PlayerPage
    }, {
      path: '/team/:detail',
      name: 'detail_team',
      component: DetailPageTeam
    }, {
      path: '/team',
      name: 'team',
      component: TeamPage
    }, {
      path: '/',
      redirect: '/landing'
    }, {
      path: '/tournament',
      name: 'tournament',
      component: TournamentPage
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return {x: 0, y: 0}
  }
})



