<template>
  <div class="container">
    <div class="profile-image">
      <h2>Jebaited</h2>
      <img src="../assets/img/blank-profile.png">
    </div>
    <div class="detail-profile">
      <div class="profile">
        <h3>Profile</h3>
      </div>
      <div class="line"></div>
      <div class="table-profile">
        <table class="table">
          <tr>
            <td>ID</td>
            <td>Dummy ID</td>
          </tr>
          <tr>
            <td>Nama</td>
            <td>Dummy Name</td>
          </tr>
          <tr>
            <td>Team</td>
            <td>Dummy Team</td>
          </tr>
          <tr>
            <td>Usia</td>
            <td>xx Tahun</td>
          </tr>
          <tr>
            <td>Divisi</td>
            <td>Dummy Divison</td>
          </tr>
          <tr>
            <td>Social Media</td>
            <td class="social-media">
              <div class="logo">
                <i class="fab fa-instagram fa-2x"></i>
              </div>
              <div class="logo">
                <i class="fab fa-facebook-square fa-2x"></i>
              </div>
            </td>
          </tr>
        </table>
      </div>
      <div class="table-achievement">
        <div class="profile">
          <h3>Achievement</h3>
        </div>
        <div class="line"></div>
        <table class="table">
          <tr>
            <th>Date</th>
            <th>Place</th>
            <th>Tournament</th>
            <th>Team</th>
            <th>Prize</th>
          </tr>
          <tr v-for="items in 5" :key="items">
            <td>Dummy Date</td>
            <td>Dummy Country</td>
            <td>Dummy Tournament</td>
            <td>Dummy Team</td>
            <td>Dummy Prize Pool</td>
          </tr>
        </table>
      </div>
    </div>
    <the-top></the-top>
  </div>
</template>

<script>
import TheTop from '../components/TheTop.vue'
export default {
  name: 'DetailPagePlayer',
  components: {TheTop}
}
</script>

<style lang="scss" scoped>
  .profile-image {
    display: grid;
    grid-template-columns: 1fr;
    margin: 30px auto;
    & > img {
      width: 300px;
      margin: 0 auto;
    }

    & > h2 {
      margin: 10px auto;
    }
  }

  .line {
    border: 1px black solid;
    margin: 5px auto;
  }

  .detail-profile {
    margin: 30px auto;
    .profile {
      & > h3 {
        margin: 0;
      }
    }
    .table-profile {
      margin-bottom: 50px;
      .social-media {
        display: grid;
        grid-template-columns: 40px 40px;
      }
    }
  }
</style>



