<template>
  <div class="container">
    <div class="box"></div>
    <div class="welcome-message">
      <h2 class="middle-text">Welcome To Join Esports</h2>
      <h4 class="middle-text">Let's join the biggest community in Indonesia</h4>
    </div>
    <div class="table-container">
      <div class="item">
        <div class="box-table"></div>
        <div class="item-table">
          <table class="konten table">
            <caption>Verified Player</caption>
            <tr>
              <th>ID</th>
              <th>Team</th>
            </tr>
            <tr v-for="items in 20" :key="items">
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy</router-link>
              </td>
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy Image</router-link>
              </td>
            </tr>
            <tr>
              <td>
                <router-link :to="{ name: 'player' }">Show All</router-link>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="item">
        <div class="box-table"></div>
        <div class="item-table">
          <table class="konten table">
            <caption>Verified Team</caption>
            <tr>
              <th>Teams</th>
            </tr>
            <tr v-for="items in 10" :key="items">
              <td>
                <router-link :to="{ name: 'detail_team', params: { detail: 'dummy' }}">Dummy</router-link>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="item">
        <div class="box-table"></div>
        <div class="item-table">
          <table class="konten table">
            <caption>Tournaments</caption>
            <tr>
              <th>Upcoming</th>
            </tr>
            <tr>
              <td></td>
            <tr>
              <th>Completed</th>
            </tr>
            <tr v-for="items in 5" :key="items">
              <td>Dummy Tournaments</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
    <the-top></the-top>
  </div>
</template>

<script>
import TheTop from '../components/TheTop'
export default {
  name: 'Main',
  components: {TheTop}
}
</script>

<style lang="scss" scoped>
  .box {
    border: 5px black solid;
    height: 300px;
    margin-bottom: 30px;
  }

  .box-table {
    border: 5px black solid;
    height: 200px;
    margin-bottom: 5px;
  }

  .middle-text {
    text-align: center;
    align:center;
  }

  .welcome-message {
    margin-bottom: 30px;

    & > h4 {
      font-weight: normal;
    }
  }

  .table-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: start;
    grid-gap: 25px;
    margin-bottom: 50px;

    .item {
      display: grid;
      grid-template-columns: 1fr;

      .item-table {
        .konten {
          margin-top: 20px;
        }
      }
    }
  }
</style>



