  <div class="footer-container">
    <div class="footer">
      <div></div>
      <div class="footer-games">
        <h3>Games</h3>
        <ul>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'pubg' }}">
              PUBG
            </router-link>
            </li>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'pubgm' }}">
              PUBGM
            </router-link>
          </li>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'dota2' }}">
              Dota 2
            </router-link>
          </li>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'ml' }}">
              Mobile Legends
            </router-link>
          </li>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'csgo' }}">
              CSGO
            </router-link>
          </li>
          <li>
            <router-link :to="{ name: 'game', params: { game: 'pb' }}">
              PB
            </router-link>
          </li>
        </ul>
      </div>
      <div class="footer-gambar">
        <img src="../assets/img/medium-logo.png">
