    </div>
  </div>
</template>

<script>
export default {
  name: 'Footer'
}
</script>

<style lang="scss" scooped>
  .footer-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 250px;
    background-color: #385446;
    color: white;
  }

  .copyright {
    left: 0;
    bottom: 0;
    width: 100%;
    height: 25px;
    background-color: grey;
    text-align: center;
  }

  .footer {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }

  .footer-games {
    & > ul {
      margin: 0;
      padding-left: 15px;
      & > li {
        & > a {
          color: white;
        }
      }
    }
  }

  .footer-gambar {
    margin: auto;
    justify-self: center;
    & > img {
      width: 175px;
    }
  }

  .footer-contact {
    justify-self: end;

    & > ul {
      margin: 0;
      padding-left: 15px;
    }
  }
</style>



