<template>
  <div class="container">
    <div class="landing-top">
      <h2>LETS JOIN THE BIGGEST</h2>
      <h2>ESPORTS COMMUNITY IN INDONESIA</h2>
      <button class="button" type="button" onclick="location.href='/';">JOIN SEKARANG</button>
    </div>
    <div class="banner-container">
      <div class="box"></div>
    </div>
    <div class="game-container">
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'pubg'}}">
          <img class="game-image" src="../assets/img/pubg.jpg">
        </router-link>
      </div>
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'pubgm'}}">
          <img class="game-image" src="../assets/img/pubgm.jpg">
        </router-link>
      </div>
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'dota2'}}">
          <img class="game-image" src="../assets/img/dota2.jpg">
        </router-link>
      </div>
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'ml'}}">
          <img class="game-image" src="../assets/img/ml.jpg">
        </router-link>
      </div>
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'csgo'}}">
          <img class="game-image" src="../assets/img/csgo.jpg">
        </router-link>
      </div>
      <div class="game-banner">
        <router-link :to="{ name: 'game', params: {game: 'pb'}}">
          <img class="game-image" src="../assets/img/pb.jpg">
        </router-link>
      </div>
    </div>
    <the-top></the-top>
  </div>
</template>

<script >
import TheTop from '../components/TheTop'
export default {
  name: 'landing',
  components: {TheTop}
}
</script>

<style lang="scss">
  .landing-top {
    margin-top: 80px;
    text-align: center;

    .button {
      margin: 0 auto;
      padding: 0 50px;
      border-radius: 20px;
    }
  }

  .banner-container {
    margin-top: 80px;
  }

  .game-container {
    margin-top: 20px;
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
  }

  .game-image {
    width: 175px
  }

  .box {
      border: 5px black solid;
      height: 300px;
    }
</style>



