<template>
  <div class="container">
    <div class="profile-image">
      <h2>Ramrum Esports</h2>
      <img src="../assets/img/dummy-team-logo.jpg">
    </div>
    <div class="detail-profile">
      <div class="heading">
        <h3>Summary</h3>
      </div>
      <div class="line"></div>
      <div class="summary">
        <p>dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy dummy </p>
      </div>
      <div class="profile">
        <div class="heading">
          <h3>Profile</h3>
        </div>
        <div class="line"></div>
        <div class="table-profile">
          <table class="table">
            <caption>Players</caption>
            <tr>
              <th>ID</th>
              <th>Nama</th>
            </tr>
            <tr v-for="items in 5" :key="items">
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy</router-link>
              </td>
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy Name</router-link>
              </td>
            </tr>
          </table>
          <table class="table second-table">
            <caption>Manager/Coach</caption>
            <tr>
              <th>ID</th>
              <th>Nama</th>
            </tr>
            <tr>
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy</router-link>
              </td>
              <td>
                <router-link :to="{ name: 'detail_player', params: { detail: 'dummy'}}">Dummy Name</router-link>
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="table-achievement">
        <div class="heading">
          <h3>Achievement</h3>
        </div>
        <div class="line"></div>
        <table class="table">
          <tr>
            <th>Date</th>
            <th>Place</th>
            <th>Tournament</th>
            <th>Team</th>
            <th>Prize</th>
          </tr>
          <tr v-for="items in 5" :key="items">
            <td>Dummy Date</td>
            <td>Dummy Country</td>
            <td>Dummy Tournament</td>
            <td>Dummy Team</td>
            <td>Dummy Prize Pool</td>
          </tr>
        </table>
      </div>
      <div class="link">
        <div class="heading">
          <h3>Links</h3>
        </div>
        <div class="line"></div>
        <div class="logo-container">
          <div class="logo">
            <i class="fab fa-instagram fa-3x"></i>
          </div>
          <div class="logo">
            <i class="fab fa-facebook-square fa-3x"></i>
          </div>
        </div>
      </div>
    </div>
    <the-top></the-top>
  </div>
</template>

<script>
import TheTop from '../components/TheTop.vue'
export default {
  name: 'DetailPagePlayer',
  components: {TheTop}
}
</script>

<style lang="scss" scoped>
  .profile-image {
    display: grid;
    grid-template-columns: 1fr;
    margin: 30px auto 20px auto;
    & > img {
      width: 300px;
      margin: 0 auto;
    }

    & > h2 {
      margin: 10px auto;
    }
  }

  .line {
    border: 1px black solid;
    margin: 5px auto;
  }

  .heading {
    & > h3 {
      margin: 10px 0;
    }
  }

  .detail-profile {
    margin: 20px 0;
    .table-profile {
      margin-bottom: 0;
    }

    .summary {
      & > p {
        text-align: justify;
      }
    }

    .link {
      .logo-container {
        display: grid;
        grid-template-columns: 50px 50px;
      }
    }
  }
</style>



